<template>
  <div v-if="PlayingData.data" class="playing-detail-page"
    :style="{backgroundImage:`url(${PlayingData.data.cover.detail})`}">
    <div class="cover" ref="cover">
      <div class="video-player" @click="VideoClick">
        <video ref="video" :src="PlayingData.data.playUrl" controls autoplay></video>
        <div class="video-cover" v-if="!isPlaying">
          <div class="icon">
            <van-icon name="play" />
          </div>
        </div>
        <div class="close" @click.stop="goBack">
          <van-icon name="cross" />
        </div>
      </div>
      <div class="information">
        <div class="title">{{PlayingData.data.title}}</div>
        <div class="author">
          <span class="tag">#{{PlayingData.data.category}}</span> /
          <span class="author-name">{{PlayingData.data.author.name}}</span>
        </div>
        <div class="description">{{PlayingData.data.description}}</div>
      </div>
      <div class="push-content">
        <h3>相关推荐</h3>
        <div class="relevant-videos">
          <div class="relevant-list">
            <RankItem v-for="(item, index) in excatRelevant" :item='item' :key="index"
              @RankItemClick="RankItemClick($event)" />
            <div class="end" v-if="!excatRelevant.length">暂无更多内容</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import RankItem from "@/components/RankItem.vue";
  export default {
    name: "playingdetailpage",
    data() {
      return {
        PlayingData: {},
        isPlaying: true,
        excatRelevant: []
      };
    },
    watch: {
      $route(n, o) {
        if (n.fullPath !== o.fullPath) { //监听路由参数是否变化
          this.PlayingData = this.$store.state.PlayingPageDate;
          this.excatRelevant = this.getExcatRelevant()
          this.$refs.cover.scrollTop=0
        }
      }
    },

    methods: {
      getExcatRelevant() {
        let b = []
        if (this.$store.state.Relevant) {
          for (let i = 0; i < 10; i++) {
            let j = parseInt(this.getRandomInt(0, this.$store.state.Relevant.length - 1))
            this.$store.state.Relevant.map((elem, index) => {
              if (index == j) {
                b.push(elem)
              }
            })
          }
        }
        return b
      },
      RankItemClick($event) {
        this.$store.commit('setPlayingPageDate', $event)
        this.$router.replace({
          path: `/playingdetailpage?id=${$event.data.id}`
        });
      },
      getRandomInt(n, m) {
        return Math.floor(Math.random() * (m - n + 1) + n);
      },
      goBack() {
        this.$router.go(-1);
        this.$store.commit("setShowBottomBar", true);
      },
      VideoClick() {
        this.isPlaying = !this.isPlaying;
        if (this.isPlaying) {
          this.$refs.video.play();
        } else {
          this.$refs.video.pause();
        }
      }
    },
    beforeRouteEnter(to, from, next) {
      next((vm) => {
        vm.excatRelevant = vm.getExcatRelevant()
        vm.PlayingData = vm.$store.state.PlayingPageDate;
      })
    },
    components: {
      RankItem
    }

  };
</script>

<style lang="less" scoped>
  .playing-detail-page {
    height: 100vh;
    overflow-anchor: auto;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    .cover {
      width: 100%;
      height: 100vh;
      overflow: auto;
      padding-top:212px;
      background-color: rgba(0, 0, 0, 0.8);

      .push-content {
        padding: 0 15px;

        color: #fff;

        .end {
          text-align: center;
          color: #6b6b6b;
        }

        h3 {
          color: #fafafa;
          font-weight: 100;
        }

        .content {
          color: #666;
        }

      }

      .video-player {
        position: fixed;
        top:0;
        left:0;
        width:100%;
        z-index:1;

        video {
          width: 100%;
          display: block;
        }

        .video-cover {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(0, 0, 0, 0.9);

          .icon {
            width: 50px;
            height: 50px;
            color: #fff;
            font-size: 40px;
            text-align: center;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;

            i {
              line-height: 50px;
            }
          }
        }

        .close {
          width: 30px;
          height: 30px;
          font-size: 20px;
          text-align: center;
          position: absolute;
          top: 0;
          left: 0;
          color: #9c9c9c;

          i {
            line-height: 30px;
          }
        }
      }

      .information {
        color: #fff;
        padding: 10px;

        .title {
          font-weight: bold;
        }

        .author,
        .description {
          font-size: 14px;
          color: #999;
          font-weight: bold;
          padding: 5px 0;
          max-height: 86px;
          overflow: auto;
        }
      }
    }
  }
</style>